import React, { useContext, useEffect, useState } from "react";
import Tabla from "../../tabla/Tabla";
import Buscador from "../Buscador";
import { PlanillaContext } from "../../../context/PlanillaContext";
import { CrudContext } from "../../../context/CrudContext";
import { crearAsistencia } from "../../../data/dataTable";
import MainModal from "../../modal/MainModal";
import { Empty } from "antd";
import Cargando from "../../cargando/Cargando";
import { notificacion } from "../../../helpers/mensajes";
import "../style/modalCrearAsistencia.css";
import dayjs from "dayjs";

const ModalCrearAsistencia = ({
  data,
  actualizarTabla,
  fechas,
  fechaCalendario,
}) => {
  const { setControlAsistencia, fechaId } = useContext(PlanillaContext);
  const [selectedAsistencia, setSelectedAsistencia] = useState(null);
  const [filterText, setFilterText] = useState("");
  const [search, setSearch] = useState([]);

  const {
    getData,
    data2,
    setData2,
    createData,
    updateData,
    modal,
    dataToEdit,
    setDataToEdit,
    setModal,
    cargando,
    setCargando,
  } = useContext(CrudContext);

  const [id, setId] = useState("");

  const filtrarFechaId = () => {
    const prueba = fechas && fechas?.filter((item) => item?.fecha == data);
    const id = prueba && prueba?.at(-1)?.id;
    if (id) {
      setId(id);
    }
  };

  useEffect(() => {
    filtrarFechaId();
  }, [data, fechas]);

  const getTrabajadorAsistencia = async () => {
    const route = `asistencia/trabajador/${id}`;

    const response = await getData(route);
    setData2(response.data);
  };
  useEffect(() => {
    if (id) {
      getTrabajadorAsistencia();
    }
  }, [id]);

  const closeModal = () => {
    setModal(false);
    setData2([]);
  };

  const handleAsistencia = async (event, e) => {
    const route = "asistencia/trabajador";
    const info = {
      asistencia_id: id,
      trabajador_id: e.dni,
      asistencia: event.target.value,
    };
    const response = await createData(info, route);
    if (response) {
      notificacion(response.status, response.msg);
      actualizarTabla();
      getTrabajadorAsistencia();
    }
  };

  const handleIngreso = async (e) => {
    const route = "asistencia/hora_ingreso";
    let obj = {
      hora_ingreso: e.target.value,
    };

    const response = await updateData(obj, data.id, route);
    if (response.status === 200) {
      actualizarTabla();
    }
  };

  useEffect(() => {
    const filtered = data2?.filter(
      (item) =>
        `${item?.apellido_paterno} ${item?.apellido_materno} ${item?.nombre}`
          ?.toLowerCase()
          ?.includes(filterText.toLowerCase()) ||
        item?.dni?.toLowerCase()?.includes(filterText.toLowerCase())
    );

    setSearch(filtered);
  }, [filterText, data2]);

  const columns = crearAsistencia(handleAsistencia);
  return (
    <MainModal
      className={"modal-asistencia"}
      title={
        dataToEdit
          ? `Editar asistencia - ${dayjs(fechaCalendario).format(
              "DD MMMM YYYY"
            )}`
          : `Registrar asistencia - ${dayjs(fechaCalendario).format(
              "DD MMMM YYYY"
            )}`
      }
      open={modal}
      width={800}
      closeModal={closeModal}
    >
      <div className="hora-ingreso">
        <div>
          <label htmlFor="">Hora de ingreso: </label>
          <input
            type="time"
            name="hora_ingreso"
            defaultValue={data?.hora_ingreso || "07:30"}
            onChange={handleIngreso}
          />
        </div>
      </div>
      <div className="buscador-container">
        <Buscador
          registrar={false}
          crear={false}
          exportar={false}
          cargar={true}
          validacion={false}
          buscador={false}
          setFilterText={setFilterText}
          // actualizar={actualizarTabla}
          actualizar={getTrabajadorAsistencia}
          fecha={data}
          setModal={setModal}
        />
      </div>
      <br />
      <Tabla columns={columns} table={search} />
    </MainModal>
  );
};

export default ModalCrearAsistencia;
